/*header*/
*{
  box-sizing: border-box;

}

:root {
	--mainColor: white;
	--mainColorLight: #5f5f5f;
	--blue: #2C5AB4;
	--gray: #5f5f5f;;
}

header {
	display:  flex;
	align-items:  center;
	justify-content: space-between;
	height: 100%;
  display: flex;
  padding: 0 2rem;
	color: var(--gray);
  width: 80%;
  margin: auto;
 
}
 
.logo-container {
  padding-top: 15px;
}


.nav-container{
  display: flex;
  width: 40%;
  text-align: end;
  padding:0;
  justify-content:flex-end;

}
  nav a {
    margin: 0 1rem;
    color: var(--gray);
    text-decoration: none;
   

  }
  nav h3{
    font-weight: 400;
  }

nav a:hover {
	color: black;
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
  display:none;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
 
 
}

header div,
nav {
	display: flex;
	align-items: center;
}

@media only screen and (max-width: 768px) {
  
  header{
    width: 100%;
    justify-content: space-between;
    
  }
  
	header .nav-btn {
    display: flex;
		visibility: visible;
		opacity: 1;
    
	}

	header nav {
		position: fixed;
		top: -100vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--blue);
		transition: 1s;
    transform: translateY(-100vh);
    color:white;
     
	}

	header .responsive_nav {
		transform: translateY(100vh);
    display: flex;
	}
  nav a:hover {
    color: rgb(237, 159, 81);
  }
  

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}
  nav .nav-close-btn :hover {
    color: rgb(237, 159, 81);
	}

	nav a {
		font-size: 1.5rem;
    color: white;
	}
 
  .logo-container h3 {
     font-size: 16px;
  }
 
}
 

