 /*rotating text*/ 
.im{
  font-size:50px;
}
.inner{
  position: relative;
  height:60px;
  line-height:60px;
  font-size:50px;
  overflow:hidden;
  display:inline-block;
  align-items: center;
  vertical-align: top;
}
.inner span{
  animation:animate 10s ease infinite;
  position:relative;
  color:#e58e26;
   
}
@keyframes animate{
  0%,100%{
    top:0;
  }
  20%{
    top:0px;
  }  
  25%{
    top:-60px;
  }
  45%{
    top:-60px;
  }
  50%{
    top:-120px;
  }
  70%{
    top:-120px;
  }
  75%{
    top:-120px;
  }
  95%{
    top: -120px;
  }
}

