/*toggle buttons */
.toggle-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
  background-color: salmon;

  
}

.toggle-background {
   
  position: relative;
  width: 200px;
  height: 50px;
  border-radius: 40px;
  background-color: var(--blue);
  transition: background-color 0.3s ease-in-out;
  
  
}

.toggle-switch {
  
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 50px;
  background-color:white;
  border-radius: 40px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: left 0.3s ease-in-out;

  
}

.switch-handle {
  position: relative;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 40px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 0.3s ease-in-out;
 
}
 

.work-mode .toggle-switch {
  left: 100px;
}

.work-mode .switch-handle {
  transform: translate(20%, -50%);
}

.play-mode .toggle-switch {
  left: 0;
}

.play-mode .switch-handle {
  transform: translate(-20%, -50%);
}

 
